body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 5s linear;
          animation: App-logo-spin infinite 5s linear;
  height: 60px;
}

.App-header {
  background-color: #0c0c0c;
  height: 100px;
  padding: 20px;
  color: white;
  margin-bottom: 50px;
  border-radius: 0 0 50px 50px;
  box-shadow: 5px 10px 40px black;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


body {
  overflow-y: scroll;
}
.series-list {
  list-style: none;
  padding: 0;
}

.result-item {
  margin: 10px auto 10px auto;
  border: 2px solid rgb(65, 65, 65);
  border-radius: 20px;
  padding: 5px;
  width: 50%;
}

.result-item-brief {
  width: 70%;
  margin-left: auto;
  margin-right: auto;
  color: rgb(87, 87, 87);
}

.result-item-title {
  -webkit-text-decoration: bold;
          text-decoration: bold;
}

.item-link {
  text-decoration: none;
  font-weight: bold;
  color: rgb(38, 57, 168);
}

.item-link:hover {
  text-decoration: underline;
}
.search-box {
  width: 40%;
  padding: 10px;
  border-radius: 20px;
  border-color: rgba(0,0,0,0);
  outline: none;
  box-shadow: 1px 2px 10px rgb(85, 85, 85);
}
.loading {
  margin: 10px;
  height: 60px;
}
.show-info {
  margin: auto;
}

.info-list {
  list-style: none;
  padding: 0px;
}

.info-list li {
  margin-top: 5px;
}

.wrapper {
  display: grid;
  grid-template-columns: auto auto;
  padding: 10px;
  width: 60%;
  margin: auto;
}

.grid-item {
  /* border: 1px solid rgba(0, 0, 0, 0.8); */
  padding: 20px;
  text-align: left;
}

.show-art {
  border-radius:10px;
  box-shadow: 5px 5px 50px black;
}

.test {
  font-size: 20px;
  text-decoration: none;
  font-weight: bold;
  color: rgb(51, 51, 51);
}
